<template>
  <el-card class="box-card" shadow="hover">
    <div slot="header" class="clearfix">
      <span class="item-name">{{ structName }}</span>
      <el-button style="float: right; padding: 3px 0" type="text" @click="click()">显示
      </el-button>
    </div>
    <div class="text item" ref="target" style="max-height: 300px;overflow-y:auto;">
      {{json}}
    </div>
  </el-card>
</template>

<script>
import {JSONFormat} from '../assets/js/jquery.json'

export default {
  name: 'StructCard',
  props: {
    structName: String,
    structContent: Object
  },
  data () {
    return {
      json: 'test',
      content: ''
    }
  },
  mounted () {
    this.title = this.structName
    this.content = JSON.stringify(this.structContent)
    this.$refs.target.innerHTML = new JSONFormat(this.content).toString()
  },
  watch: {
  },
  methods: {
    click () {
      console.log('structCard:' + JSON.stringify(this.content) + this.title)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "src/assets/css/struct-card.scss";
</style>



