<template>
  <div class="card-page">
    <span class="title-text">结构体可视化</span>
    <el-button style="float: right; padding: 3px 0" type="text" @click="click">show data</el-button>
    <StructContent v-for="(value,key,index) in Data" :key="index" :name="key" :structData="value"/>
  </div>
</template>

<script>

import 'vue-foldable/dist/vue-foldable.css'
import StructContent from './StructContent'

export default {
  name: 'StructView',
  props: {
    Data: Object
  },
  components: {
    StructContent
  },
  data () {
    return {
      isActive: false,
      toggle: true,
    }
  },
  updated () {
  },
  watch: {
  },
  methods: {
    click () {
      console.log('structView:' + JSON.stringify(this.Data))
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/css/struct-view";
</style>



