<template>
  <div class="struct-content" style="background-color: #aa475f">
    <VueFolder :noMask="true">

      <el-button style="float: right; padding: 5px 10px" type="text" @click="click">show struct</el-button>
      <span class="content-title">{{ name }}</span>
      <StructCard v-for="(value,key,index) in structData" :key="index" :structName="key" :structContent="value"/>
    </VueFolder>
  </div>
</template>

<script>
import VueFolder from 'vue-foldable'
import StructCard from './StructCard'

export default {
  name: 'StructContent',
  props: {
    structData: Object,
    name: String
  },
  components: {
    VueFolder,
    StructCard
  },
  data () {
    return {
      title: ''
    }
  },
  watch: {
  },
  methods: {
    click () {
      console.log('structContent:' + JSON.stringify(this.structData))
    }
  }
}
</script>

<style lang="scss" scoped>
@import "./src/assets/css/struct-content.scss";
</style>



