<template>
  <div id="app">
    <input v-model="json" placeholder="paste json here"/>
    <div class="page">
      <StructView :Data="data"/>
    </div>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld'
import StructView from './components/structView'

export default {
  name: 'App',
  components: {
    HelloWorld,
    StructView
  },
  data () {
    return {
      json: '',
      data: {}
    }
  },
  watch: {
    json () {
      console.log('json:' + this.json)
      this.data = JSON.parse(this.json)
    }
  },
  mounted () {
    window.addEventListener('message', event => {
      const message = event.data
      switch (message.cmd) {
        case 'dataLoad':
          // console.log('收到dataLoad请求:' + JSON.stringify(message.data))
          this.data = message.data.property
          break
        // case 'vscodeCallback':
          // console.log(message.data)
          // (callbacks[message.cbid] || function () { })(message.data)
          // delete callbacks[message.cbid]
          // break
        default:
          break
      }
    })
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
@import "./src/assets/css/app.scss";
</style>



